import React, { Component } from 'react';
import { Container, Row, Col, ListGroup, ListGroupItem } from 'reactstrap';



export class Australia extends Component {
  render() {
    return (
      <div className="blogPost">
        <Container>
          <Row>
              <Col xs="12" sm="4" className="category category--australia">
              <div class="overlay">
                <div class="textImage">Australia</div>
                <div class="textImageSubheadline">Work and travel</div>
            </div>
              </Col>
              <Col xs="12" sm="8" className="blog__content">

                    <p>Coming soon. Starting 24th October in Sydney.</p>
               
              </Col>
            </Row>
          </Container>
      </div>
    );
  }
}



