import React, { Component } from 'react';
import { Container, Row, Col, ListGroup, ListGroupItem } from 'reactstrap';



export class Isfates extends Component {
  render() {
    return (
      <div className="blogPost">
        <Container>
          <Row>
              <Col xs="12" sm="4" className="category category--isfates">
              <div class="overlay">
                <div class="textImage">ISFATES</div>
                <div class="textImageSubheadline">Binational studies</div>
            </div>
              </Col>
              <Col xs="12" sm="8" className="blog__content">
                <img src="https://docs.univ-lorraine.fr/Formulaires/logos/comp-interface-isfates.jpg" class="blog__image" />
                    <p>My computer science studies at the french-german university school <a href="https://www.dfhi-isfates.eu/fr/">ISFATES/DFHI</a>. <br />
                        <br />
                        Spread over three years in two countries: <br />
                        <ul>
                            <li>1st year in Metz (France)</li>
                            <li>2nd year in Saarbrcken (Germany)</li>
                            <li>3rd year half Metz (in my case ERASMUS) and half internship in Germany with a bachelor thesis.</li>
                        </ul>
                    </p>

                    <div class="blogPost__iconContainer">
                  <i class="fas fa-university"></i>
                  <div class="iconContainerText">
                  <a href="https://documents.wingi.net/Info/Info_desktop_en.pdf">University Curriculum</a></div>
                </div>

                <br /> <br />

                    <p>
                      In order to complete by bachelor, I had three months to write a bachelor thesis about sharing styles between web components.
                      </p>

                       <div class="blogPost__iconContainer">
                  <i class="fas fa-pen-fancy"></i>
                  <div class="iconContainerText">
                  <a href="https://github.com/luiswill/bachelorWebComponentStyling/blob/master/thesis.pdf">Bachelor Thesis</a></div>
                </div>

                
                
              </Col>
            </Row>
          </Container>
      </div>
    );
  }
}



