import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Category } from './Category';
import { Link, Route } from "react-router-dom";


export class Home extends Component {
  render() {
    return (
        
        <Container fluid="true">
        <Row>
          <div class="introHome">
            <b class="headline">Hi, I'm Luis <i class="fas fa-hand-peace"></i></b> <br />
            <p>I'm a passionate software student. I love swimming and tech in general <i class="fas fa-swimmer"></i><i class="fas fa-laptop"></i></p>
          </div>
          </Row>
        <Row noGutters="true">
            <Col xs="12" sm="6" lg="3"><Link to="/australia"><Category className="category category--australia" categoryName="Australia" categoryDescription="Work and Travel"></Category></Link></Col>
            <Col xs="12" sm="6" lg="3"><Link to="/virtualIdentity"><Category className="category category--vi" categoryName="Virtual Identity" categoryDescription="Internship"></Category></Link></Col>
            <Col xs="12" sm="6" lg="3"><Link to="/Isfates"><Category className="category category--isfates" categoryName="ISFATES" categoryDescription="Binational studies"></Category></Link></Col>
            <Col xs="12" sm="6" lg="3"><Link to="/erasmus"><Category className="category category--erasmus" categoryName="ERASMUS" categoryDescription="Limerick, Ireland"></Category></Link></Col>
            <Col xs="12" sm="6" lg="3"><Link to="/foodSharing"><Category className="category category--foodSharing" categoryName="Food Sharing" categoryDescription="Android App"></Category></Link></Col>
        </Row>

        </Container>
    );
  }
}



