import React, { Component } from 'react';
import { Container, Row, Col, ListGroup, ListGroupItem } from 'reactstrap';



export class VI extends Component {
  render() {
    return (
      <div className="blogPost">
        <Container>
          <Row>
              <Col xs="12" sm="4" className="category category--vi">
              <div class="overlay">
                <div class="textImage">Virtual Identity</div>
                <div class="textImageSubheadline">Internship</div>
            </div>
              </Col>
              <Col xs="12" sm="8" className="blog__content">

                    <p>As part of my university curriculum I was an intern in a web agency named Virtual Identity from January 2018 to end of June 2018 as a front-end developer.</p>
                
                <h5 class="blogPost__headline">Technologies used</h5>
                <ul class="blogPost__list">
                  <li>HTML/CSS and JavaScript</li>                
                  <li>TypeScript</li>
                  <li>jQuery</li>
                  <li>Front-end Framework <a href="https://github.com/biotope/biotope-build">Biotope</a></li>
                  <li>Gulp</li>
                  <li>Handlebars</li>
                  <li>Yarn</li>
                  <li>Sass</li>
                  <li><a href="http://getbem.com/">BEM methodology</a></li>
                </ul>

                 <h5 class="blogPost__headline">Code style learned</h5>
                <ul class="blogPost__list">
                  <li>Good variable and function naming</li>
                  <li><b>KISS</b>. Keep it Simple and Stupid</li>                
                  <li>Atomar, small functions doing their job really good</li>
                  <li>ES6 Syntax</li>
                  <li>Flexbox</li>
                </ul>

                <h5 class="blogPost__headline">Tools used</h5>
                <ul class="blogPost__list">
                  <li>Webstorm - JetBrains</li>                
                  <li>Visual Studio</li>
                  <li>JIRA - Atlassian</li>
                </ul>

                <h5 class="blogPost__headline">Cultural new knowledge</h5>
                <ul class="blogPost__list">
                  <li>Working with other front-end developers</li>                
                  <li>Being social with company's people I am not working with</li>
                  <li>Being on time</li>
                  <li>Working with strict deadlines</li>
                  <li>Being flexible</li>
                </ul>

                  <h5 class="blogPost__headline">Monthly reports</h5>
                <div class="blogPost__iconContainer">
                  <i class="far fa-folder-open"></i>
                  <div class="iconContainerText">
                  <a href="https://drive.google.com/drive/folders/1-wiTtVfuBwKKHii7pa0iUh1FM5jVA2ps?usp=sharing">Google Drive Folder</a></div>
                </div>
              </Col>
            </Row>
          </Container>
      </div>
    );
  }
}



