import React, { Component } from 'react';
import { Container, Row, Col, ListGroup, ListGroupItem } from 'reactstrap';


export class Contact extends Component {
  render() {
    return (
      <div className="blogPost">
        <Container>
          <Row>
              <Col xs="12" sm="4" className="category">
              <img src={require('../../images/luisPicture.jpg')} />
              </Col>
              <Col xs="12" sm="8" className="blog__content">
                <h2>Luis WILLNAT</h2>
                26/06/1997 

                <br /><br />

                French-german, born in Mulhouse (France). <br />
                <a href={require('../../images/cv_luis.pdf')}>Curriculum vitae</a>
                <br />
                <br />
                <a href="https://github.com/luiswill/">Github</a>

                <br/> 
                <a href="mailto:luis.willnat.lw@gmail.com">luis.willnat.lw@gmail.com</a>


                <br /><br />
                Favourite language: Python <br /> 
                Favourite pizza: Pizza Atlantic (thanks <a href="https://www.al-bacio.de/">Al bacio)</a><br />


                
              </Col>
            </Row>
          </Container>
      </div>
    );
  }
}



