import React, { Component } from 'react';
import logo from './logo.svg';
import { BlogPost } from "./components/BlogPost";
import { Home } from "./components/Home";
import { Australia } from "./components/posts/Australia";
import { VI } from "./components/posts/VI";
import { Isfates } from "./components/posts/Isfates";
import { Erasmus } from "./components/posts/Erasmus";
import { Contact } from "./components/posts/Contact";
import { FoodSharing } from "./components/posts/FoodSharing";
import { Link, Route, Redirect } from "react-router-dom";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem } from 'reactstrap';
  
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Redirect from="/" to="/home" />
         <Navbar color="light" light expand="md">
          <NavbarBrand><Link to="/home">Luis Willnat</Link></NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse navbar>
            <Nav className="ml-auto" navbar>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Professional
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    <Link to="/virtualIdentity">Virtual Identity</Link>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Personal
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    <Link to="/foodSharing">Food Sharing</Link>
                  </DropdownItem>
                  <DropdownItem>
                  <Link to="/foodSharing">Other applications</Link>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Academic
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    <Link to="/Isfates">ISFATES</Link>
                  </DropdownItem>
                  <DropdownItem>
                  <Link to="/erasmus">ERASMUS Limerick</Link>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <NavItem className="contact nav-link">
              <Link to="/contact">Contact</Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        <div>
          <Route path="/home" component={Home}/>
            <Route path="/australia" component={Australia}/>
            <Route path="/virtualIdentity" component={VI}/>
            <Route path="/isfates" component={Isfates}/>
            <Route path="/foodSharing" component={FoodSharing}/>
            <Route path="/erasmus" component={Erasmus}/>
            <Route path="/contact" component={Contact}/>
        </div>
      </div>
    );
  }
}

export default App;



