import React, { Component } from 'react';
import './Category.css';

export class Category extends Component {
  render() {
    return (
      <div className={this.props.className}>
        <div class="overlay">
            <div class="textImage">{this.props.categoryName}</div>
            <div class="textImageSubheadline">{this.props.categoryDescription}</div>
        </div>
      </div>
    );
  }
}



